<template>
    <div :id="id" class="gauge">
        <img class="gauge__blur" alt="Gauge blur" :src="blurImageURL" />

        <img
            :src="backgroundImageURL"
            alt="Gauge back"
            class="gauge__background"
        />

        <p v-if="showLabels" class="gauge__changed">
            <i
                :class="[
                    'gauge__arrow',
                    { gauge__arrow_inverted: isValueDecreased },
                ]"
            />

            <span class="gauge__text">
                {{ valueDifference }}
            </span>
        </p>

        <apexchart
            class="gauge__graph"
            type="radialBar"
            height="204"
            :options="chartOptions"
            :series="series"
        />
    </div>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */
export default {
    props: {
        id: {
            type: String,
            required: true,
        },

        value: {
            type: Number,
            required: true,
        },

        gradientName: {
            type: String,
            required: true,
        },

        showLabels: {
            type: Boolean,
            default: true,
        },

        isValueDecreased: {
            type: Boolean,
            default: true,
        },

        valueDifference: {
            type: Number,
            default: 0,
        },
    },

    data() {
        return {
            previousValue: 0,

            series: [this.value],

            chartOptions: {
                chart: {
                    // offsetY: -8,
                    offsetY: -9,
                    offsetX: -1,
                    height: 204,
                    type: 'radialBar',
                    toolbar: {
                        show: false,
                    },
                    animations: {
                        enabled: true,
                        easing: 'easeinout',
                        speed: 800,
                        dynamicAnimation: {
                            enabled: true,
                            speed: 800,
                        },
                    },
                },

                states: {
                    hover: {
                        filter: {
                            type: 'none',
                        },
                    },
                },

                plotOptions: {
                    radialBar: {
                        startAngle: -155,
                        endAngle: 155,
                        hollow: {
                            show: false,
                            size: '77%', // percentage
                        },

                        track: {
                            show: false,
                        },

                        dataLabels: {
                            show: this.showLabels,
                            name: {
                                offsetY: 44,
                                show: true,
                                color: '#fff',
                                fontSize: '20px',
                                fontFamily: 'SFUIDisplay',
                            },
                            value: {
                                formatter(val) {
                                    return parseInt(val, 10);
                                },
                                offsetY: -3,
                                offsetX: -15,
                                color: '#fff',
                                fontSize: '42px',
                                show: true,
                                fontFamily: 'SFUIDisplay',
                            },
                        },
                    },
                },

                fill: {
                    type: 'image',
                    image: {
                        src: [
                            require(`@/assets/images/bitmaps/gradient-${this.gradientName}.jpg`),
                        ],
                    },
                },

                stroke: {
                    lineCap: 'round',
                },

                labels: ['%'],
            },
        };
    },

    computed: {
        blurImageURL() {
            return require(`@/assets/images/bitmaps/blur-${this.gradientName}.png`);
        },

        backgroundImageURL() {
            return require(`@/assets/images/bitmaps/circle-dotted-${this.gradientName}.png`);
        },
    },

    watch: {
        value(newValue) {
            // eslint-disable-next-line prefer-destructuring
            this.series[0] = newValue;
            this.chartOptions = { ...this.chartOptions };
        },
    },
};
</script>

<style lang="css" scoped>
.gauge {
    display: flex;
    position: relative;
}

.gauge__blur {
    position: absolute;
    top: -34px;
    object-fit: contain;
    left: 50%;
    transform: translateX(-50%);
}

.gauge__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.gauge__changed {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
}

.gauge__arrow {
    width: 13px;
    height: 14px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('~@/assets/images/icons/arrow-bold-up.svg');
    filter: invert(100%);
    margin-right: 3px;
    transition: transform 0.4s ease-in-out;
}

.gauge__arrow_inverted {
    transform: scaleY(-1);
}

.gauge__text {
    font-family: 'SFUIDisplay';
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
}
</style>
